// 
import immutable from 'immutable'
import cookies from '../utils/uCookie.js'
// 
import {
  POST_LOGIN, POST_REGISTER, LOGOUT
} from '../constants/auth.js'
import {
  USER_TOKEN, USER_INFO
} from '../constants/configs.js'

let defaultState = immutable.fromJS({
  userInfo: {},
  isLogined: cookies.get(USER_TOKEN) ? true : false,
  type: ''
})

export default function reducer(state=defaultState,action){
  switch (action.type) {
    case POST_LOGIN:
      const payload = {...action.payload}
      if (payload.type==='SUCCESS') {
        const expires = new Date().getTime() + 365* 24 * 3600 * 1000 // 
        const {token, id, username} = payload.response
        const options = {
          path: '/',
          expires: new Date(expires)
        }
        cookies.set(USER_TOKEN, token, options) // token
        state = state.set('type', 'SUCCESS')
          .set('isLogined', true)
          .set('userInfo', {id, username})
      }else{
        state = state.set('type', 'FAILED')
      }
      return state
    case POST_REGISTER:
      return state.merge({...action.payload})
    case LOGOUT:
      cookies.clear()
      return state.clear()
    default:
      return state
  }
}
